import React from "react";
import { Row, Col, Button, Container, Form } from "react-bootstrap";
import { MdAddShoppingCart, MdRemoveShoppingCart } from "react-icons/md";
import styles from "../styles/BeatComponent.module.css";
import { useCart, usePopup } from "../hooks";
import AudioPlayer from "./AudioPlayer";

const BeatComponent = (props) => {
  const { cart, addToCart, removeFromCart, leaseOptions } = useCart();
  const [setPopupIsOpen, setPopupInfo] = usePopup();

  const [isShowingDetails, setShowingDetails] = React.useState(false);
  const [inCart, setInCart] = React.useState(false);

  React.useEffect(() => {
    if (cart[props.id]) {
      setInCart(true);
    } else {
      setInCart(false);
    }
  }, [cart, props.id, setInCart]);
  // creates reference for form select lease
  const selectFormRef = React.useRef();

  // "    " for html rendering
  const tabsTemplate = <>&nbsp;&nbsp;&nbsp;&nbsp;</>;

  // formats beat tags into a single string with a hashtag in front of each tag
  const getTags = (tags) => tags.map((tag) => `#${tag} `);

  const handleAddToCart = () => {
    const collected = parseInt(selectFormRef.current.value);
    // check value of select form - make sure a valid value is selected
    if (collected !== 1 && collected !== 2) {
      setPopupInfo({
        title: "Warning",
        descript: "Please select a lease type for this beat before continuing",
      });
      setPopupIsOpen(true);
      return null;
    }
    const cartVal = cart[props.id];
    if (cartVal) {
      const popupInfoOut = { title: "Warning", descript: "" };
      if (cartVal === collected) {
        popupInfoOut.descript =
          "This lease selection has already been added to your cart. Purchases are limited to one lease per beat.";
      } else {
        popupInfoOut.descript =
          "A lease selection has already been added to your cart. This action has updated the lease type for this beat. Purchases are limited to one lease per beat.";
      }

      setPopupInfo(popupInfoOut);
      setPopupIsOpen(true);
    }
    addToCart(props.id, props.name, collected);
  };

  const handleRemoveItem = () => {
    removeFromCart(props.id);
  };

  return (
    <Container className={styles.componentWrapper}>
      <Row className={styles.component}>
        <Col lg={2} className={styles.coverStack}>
          {/* TODO: remove next/Image implement */}
          <img src={props.coverURL} alt={props.name} className={styles.cover} />
        </Col>
        <Col lg={7} className={styles.audioStack}>
          <Row className={styles.audioStackElem}>
            <AudioPlayer srcUrl={props.audioURL} idx={props.id} />
          </Row>
          <Row className={styles.audioStackElem}>
            <div className={styles.title}>
              <h2 className={styles.titleText}>
                {`// `}
                {props.name}
              </h2>
            </div>
            <p
              className={styles.showDetails}
              onClick={() => {
                setShowingDetails(!isShowingDetails);
              }}
            >
              show details
            </p>
          </Row>
          {isShowingDetails ? (
            <Row className={styles.details}>
              <Col className={styles.tagsCol}>
                <b>tags</b>:
                <i>
                  {tabsTemplate}
                  {getTags(props.tags)}
                </i>
              </Col>
              <Col className={styles.keyCol}>
                <b>key</b>:
                <i>
                  {tabsTemplate}
                  {props.musicKey}
                </i>
              </Col>
              <Col className={styles.tempoCol}>
                <b>tempo</b>:
                <i>
                  {tabsTemplate}
                  {props.bpm} bpm&nbsp;
                </i>
              </Col>
            </Row>
          ) : (
            <></>
          )}
        </Col>
        <Col className={styles.cartStack} lg={3}>
          <Form.Control
            ref={selectFormRef}
            size="sm"
            as="select"
            aria-label="Beat lease select"
            className={styles.selectButtonElem}
          >
            <option value={0}>{leaseOptions[0].descript}</option>
            <option value={1}>{leaseOptions[1].descript}</option>
            <option value={2}>{leaseOptions[2].descript}</option>
          </Form.Control>
          <Container className={styles.cartStackElem}>
            <Button
              size="sm"
              onClick={handleAddToCart}
              className={styles.addToCartButton}
              variant="dark"
            >
              Add
              <MdAddShoppingCart className={styles.addToCartIcon} />
            </Button>
            {inCart ? (
              <>
                <Button
                  size="sm"
                  onClick={handleRemoveItem}
                  className={styles.rmFromCartButton}
                  variant="danger"
                >
                  Remove
                  <MdRemoveShoppingCart className={styles.addToCartIcon} />
                </Button>
              </>
            ) : (
              <></>
            )}
          </Container>

          {inCart ? (
            <>
              <Container className={styles.inCartElem}>
                <h6 className={styles.inCartTitle}>In cart:</h6>
                <p className={styles.inCartDescript}>
                  {cart[props.id] ? (
                    `(${leaseOptions[cart[props.id].type].descript})`
                  ) : (
                    <></>
                  )}
                </p>
              </Container>
            </>
          ) : (
            <></>
          )}
        </Col>
      </Row>
    </Container>
  );
};
export default BeatComponent;
