import React from "react";
import { Container } from "react-bootstrap";
import styles from "../styles/Popup.module.css";

const Popup = (props) => {
  if (!props.isOpen) return null;
  return (
    <>
      <Container className={styles.popupWrapper}>
        <Container className={styles.windowWrap}>
          <Container className={styles.window}>
            <Container className={styles.content}>
              <Container className={styles.title}>
                <h1 className={styles.titleText}>{props.title}</h1>
              </Container>
              <Container className={styles.descript}>
                <p>{props.descript}</p>
                <p className={styles.closePopup} onClick={props.onClose}>
                  click here to dismiss
                </p>
              </Container>
            </Container>
            <Container className={styles.x} onClick={props.onClose}>
              [x]
            </Container>
          </Container>
        </Container>
      </Container>
    </>
  );
};

export default Popup;
