import React from "react";
import { useCart, useSrc } from "../hooks";
import { Row, Col, Form, Container, Button } from "react-bootstrap";
import { MdRemoveShoppingCart } from "react-icons/md";
import styles from "../styles/CartComponent.module.css";

const CartComponent = (props) => {
  const { addToCart, removeFromCart, leaseOptions } = useCart();
  const { getFromSrc, srcLoaded } = useSrc();
  const [currentSelection, setCurrentSelection] = React.useState(0);
  const selectFormRef = React.useRef();
  const [coverURL, setCoverURL] = React.useState(
    "https://cdn.pixabay.com/photo/2015/04/23/22/00/tree-736885__480.jpg"
  );

  const handleRemoveItem = () => {
    removeFromCart(props.id);
  };

  React.useEffect(() => {
    selectFormRef.current.value = props.type;
    setCurrentSelection(props.type);
  }, [props.type, setCurrentSelection]);

  React.useEffect(() => {
    const url = getFromSrc(props.id, "coverURL");
    setCoverURL(url);
  }, [srcLoaded, getFromSrc, props.id, setCoverURL]);

  const handleChange = () => {
    const collected = parseInt(selectFormRef.current.value);
    addToCart(props.id, props.name, collected);
    setCurrentSelection(collected);
  };

  return (
    <Container className={styles.componentWrapper}>
      <Row className={styles.component}>
        <Col lg={3}>
          <img src={coverURL} alt={props.name} className={styles.cover} />
        </Col>
        <Col lg={7} className={styles.contentWrapper}>
          <Container className={styles.title}>
            <h3 className={styles.titleText}>
              {`// `}
              {props.name}
            </h3>
          </Container>
          <Container className={styles.detailElem}>
            <Form.Control
              ref={selectFormRef}
              size="md"
              as="select"
              aria-label="Beat lease select"
              className={styles.selectElem}
              onChange={handleChange}
            >
              <option value={1}>{leaseOptions[1].descript}</option>
              <option value={2}>{leaseOptions[2].descript}</option>
            </Form.Control>
          </Container>
          <Container className={styles.cartInteractElem}>
            <Button
              size="sm"
              onClick={handleRemoveItem}
              className={styles.rmFromCartButton}
              variant="danger"
            >
              Remove&nbsp;
              <MdRemoveShoppingCart className={styles.addToCartIcon} />
            </Button>
            <h5 className={styles.currentPrice}>
              {` $ `}
              {leaseOptions[currentSelection].price.toFixed(2)}
            </h5>
          </Container>
          <Container className={styles.detailElem}></Container>
        </Col>
      </Row>
    </Container>
  );
};

export default CartComponent;
