import React from "react";
import { Container } from "react-bootstrap";
import { useCart, useSrc } from "../hooks";
import { CartComponent } from "../components";

const Cart = () => {
  const [cartComponentArr, setCartComponentArr] = React.useState([]);
  const { cart, cartTotal } = useCart();
  const { srcLoaded } = useSrc();

  const renderComponents = React.useCallback(() => {
    console.log("callback");
    if (Object.keys(cart).length > 0) {
      let newArr = [];
      Object.keys(cart).forEach((key) => {
        newArr.push(
          <CartComponent
            id={cart[key].id}
            name={cart[key].title}
            type={cart[key].type}
            key={key + "cart"}
          />
        );
      });
      setCartComponentArr(newArr);
    } else {
      setCartComponentArr([]);
    }
  }, [cart]);

  React.useEffect(() => {
    if (srcLoaded) {
      renderComponents();
    }
  }, [srcLoaded, renderComponents, cart]);

  return (
    <Container>
      {srcLoaded ? cartComponentArr : <></>}
      <Container>
        Total: {cartTotal} {String(srcLoaded)}
      </Container>
    </Container>
  );
};

export default Cart;
